.frontpage header.header.headroom--top{
	.header-container{
		// background: linear-gradient(180deg, rgba(#000,.7) 0%, rgba(#000,0) 100%);
	}//header-container
}

header.header-01 .header-container .header-left{
	width: 114px;
}

#menu .menu__container > li > a{
	padding-top:40px;
	padding-bottom:40px;
	padding-left:18px;
	padding-right:18px;
}


.header .menu{
	font-family: Montserrat, Helvetica, Arial, sans-serif;
	font-weight: 400;
	font-style: normal;
	line-height: 1.2;
	letter-spacing: 0em;
	font-size: 16px;
}



.darknav{

	[data-row-skin=dark] header.header #menu .menu__container > li > a, 
	[data-row-skin=dark] header.header #menu .menu__container > ul > li > a, 
	header.header.light #menu .menu__container > li > a, 
	header.header.light #menu .menu__container > ul > li > a{
		color: #333;
	}

}//darknav


[data-row-skin=dark] header.header #menu .menu__container > li > a, 
[data-row-skin=dark] header.header #menu .menu__container > ul > li > a, 
header.header.light #menu .menu__container > li > a, 
header.header.light #menu .menu__container > ul > li > a{
	font-weight: 300;
}