@import 'wilson';

$blue: #1157C4;



body{
	font-family: 'Montserrat', Helvetica, Arial, sans-serif;
	font-weight: 300;
	font-style: normal;
	line-height: 1.8;
	// letter-spacing: -.5px;
	color: #666666;
	font-size: 15px;
}//body





.insight-testimonials.light{

	.slick-slider{

		.inner{
			width: 80%;
			margin: auto;
		}//inner


		.info .author{

			.name{
				color: #666;
				font-size: 16px;
				font-weight: 400;
			}//name

			.tagline{
				color: $blue;
				font-size: 14px;
				letter-spacing: .4px;
				font-weight: 500;
				opacity: 1;
			}//tagline

		}//author

		.text{
			color: #666;
			font-size: 17px;
			line-height: 1.5;
		}//text


		.slick-dots li button{
			border-color: #666;
			width: 13px;
			height: 13px;
		}
		.slick-dots li.slick-active button{
			border-color: $blue;
		}

	}//slick-slider

}//.insight-testimonials.light





.insight-button.size-medium a,
.insight-button.style-border.size-medium a{
	height: 45px;
	line-height: 45px;
	padding-left: 30px;
	padding-right: 30px;
}//insight-button


.insight-button.style-border.size-medium a{
	line-height: 40px;
}






.insight-testimonials.light .slick-slider .info .author .tagline{
	line-height: 1.3;
	padding-top: 10px;
	display: block;
}

.insight-testimonials .slick-slider .text{
	margin-top: 20px;
}





@import 'header';
@import 'footer';